@import url('https://fonts.googleapis.com/css2?family=Josefin+Slab&family=Patrick+Hand&display=swap');


a {
    font-family: "Josefin Slab";
    font-size: 2.5vmax;
    background-image: linear-gradient(#fc7e8d, #a8a9e7);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}



.art-carousel{
    font-family: "Patrick Hand";
    font-size: 2vmax;
}

h1 {
    font-family: "Josefin Slab";
    font-size: 4.5vmax;
    background-image: linear-gradient(#fc7e8d, #a8a9e7);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

@media screen and (min-width: 600px){
    a {
        font-family: "Josefin Slab";
        font-size: 2vmax;
        background-image: linear-gradient(#fc7e8d, #a8a9e7);
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text;
    }

    h1 {
        font-family: "Josefin Slab";
        font-size: 3.5vmax;
        background-image: linear-gradient(#fc7e8d, #a8a9e7);
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text;
    }

    .art-carousel{
        font-family: "Patrick Hand";
        font-size: 1.5vmax;
    }
}

