.menu-icon__container {
    z-index: 1000;
    cursor: pointer;
    height: 70px;
    width: 95px;
    display: flex;
    overflow: hidden;
    position: absolute;
    top: 0px;
    left: 0px;
  }
  @media screen and (min-width: 600px) {
    .menu-icon__container {
      height: 100px;
      width: 100px;
    }
  }
  .menu-icon-circle {
    background-color: #fff;
    border-radius: 50%;
    -webkit-transform-origin: center;
            transform-origin: center;
    position: absolute;
    top: 50%;
    left: 50%;
    box-shadow: 2px 2px 5px -2px rgba(0, 0, 0, 0.6);
  }
  .menu-icon {
    -webkit-transform-origin: center;
            transform-origin: center;
    position: absolute;
    top: calc(50% - 12.5px);
    left: calc(50% - 25px);
    height: 25px;
    width: 50px;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .menu-icon > div {
    height: 2px;
    width: 100%;
    border-radius: 2px;
    transition: 0.15s all ease-in-out;
    -webkit-transform-origin: left;
            transform-origin: left;
    background-image: linear-gradient(
      to right,
      rgb(138, 134, 190),
      rgb(238, 224, 251)
    );
  }
  .menu-icon > div:nth-of-type(2) {
    width: 40%;
  }
  
  /* menu icon on hover */
  @media screen and (min-width: 600px) {
    .menu-icon__container:hover > .menu-icon > div:nth-of-type(2) {
      margin-left: 20%;
    }
    .menu-icon__container:hover > .menu-icon-circle.menu-open {
      -webkit-animation: menu-icon-circle-anim 0.4s 1;
              animation: menu-icon-circle-anim 0.4s 1;
      -webkit-animation-delay: 0.15s;
              animation-delay: 0.15s;
    }
  }
  
  /* menu open */
  .menu-icon__container.menu-open > .menu-icon > div {
    height: 1px;
  }
  .menu-icon__container.menu-open > .menu-icon > div:nth-of-type(1) {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    width: 35px;
  }
  .menu-icon__container.menu-open > .menu-icon > div:nth-of-type(2) {
    opacity: 0;
    margin-left: 0%;
  }
  .menu-icon__container.menu-open > .menu-icon > div:nth-of-type(3) {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    width: 35px;
  }
  
  .menu-list-container {
    pointer-events: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    overflow: hidden;
    position: absolute;
    background-color: rgb(0, 0, 0,0.1);
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

  }
  .menu-list {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    -webkit-transform-origin: center;
            transform-origin: center;
    transition: 0.1s all ease-in-out;
    background-color: rgba(0, 0, 0, 0.8);
    -webkit-transform: scale(3);
            transform: scale(3);
    opacity: 0;
  }
  .menu-item {
    display: block;
    color: #fff;
    cursor: pointer;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px 0px;
    text-decoration: none;
  }
  .menu-item:last-of-type {
    margin-bottom: 0px;
  }
  .menu-item__title {
    display: inline-block;
    position: relative;
    line-height: 1;
    font-size: 5vmax;
    font-weight: 300;
    font-family: "Josefin Slab", sans-serif;
    text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
    z-index: 100;
    background-image: linear-gradient(#fc7e8d, #a8a9e7);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
  }

  .menu-item__title:after {
      content: '';
      position: absolute;
      width: 100%;
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
      height: 0.1vmax;
      bottom: 0;
      left: 0;
      background-color: #f3f3f3;
      -webkit-transform-origin: bottom right;
              transform-origin: bottom right;
      transition: -webkit-transform 0.25s ease-out;
      transition: transform 0.25s ease-out;
      transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
  }
  .menu-item__title:hover:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
  }
  .menu-item__subtitle {
    margin-top: 1%;
    font-size: 1vmax;
    font-family: "Patrick Hand";
    font-weight: 300;
  }

  @media screen and (min-width: 600px) {
    .menu-item:hover > .menu-item__background {
      width: 200px;
    }
  }
  
  /* menu open */
  .menu-list-container.menu-open {
    pointer-events: all;
    z-index: 100;
  }
  .menu-list-container.menu-open > .menu-list {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  .menu-list-container.menu-open ~ .App-particles__container canvas {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  .menu-list-container.menu-open ~ .App {
    pointer-events: none;
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }
  .menu-list-container.menu-open ~ .App .App-text {
    opacity: 0;
  }
  
  @keyframes menu-icon-circle-anim {
    0% {
      width: 0px;
      height: 0px;
      opacity: 0;
      top: 50%;
      left: 50%;
    }
    70% {
      width: 100px;
      height: 100px;
      opacity: 0.05;
      top: calc(50% - 50px);
      left: calc(50% - 50px);
    }
    100% {
      width: 100px;
      height: 100px;
      opacity: 0;
      top: calc(50% - 50px);
      left: calc(50% - 50px);
    }
  }
  @-webkit-keyframes menu-icon-circle-anim {
    0% {
      width: 0px;
      height: 0px;
      opacity: 0;
    }
    70% {
      width: 100px;
      height: 100px;
      opacity: 0.3;
    }
    100% {
      width: 150px;
      height: 150px;
      opacity: 0;
    }
  }
