html,
body {
  z-index: -1;
  margin: 0px;
  padding: 0px;
  color: rgb(223, 223, 223);
  height: 100%;
  background-color: #040507;
  overflow: auto;
}
#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.App a {

  transition: 0.2s all ease-in-out;
}
.App a:hover {
  opacity: 0.75;
}

.App {
  z-index: 10;
  text-align: center;
  margin: 0px 24px;
  transition: 0.1s all ease-in-out;
  -webkit-animation: app-enter-anim 0.1s ease-out;
          animation: app-enter-anim 0.1s ease-out;
}
.App-text {
  position: relative;
  top: 50%;
  opacity: 1;
  transition: 0.1s all ease-in-out;
  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */

}
.App-particles__container {
  overflow: hidden;
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 0;
}
.App-particles__container canvas {
  transition: 0.2s all ease-in-out;
  z-index: 0;
  height: 100%;
  width: 100%;
}

.App-text__title {
  font-weight: 400;
  font-size: 3.5vmax;
  letter-spacing: 0.5vmax;
  font-family: "Josefin Slab", cursive;
}
.App-text__body {
  margin-top: 12px;
  font-weight: 400;
  font-size: 2vmax;
  font-family: "Patrick Hand", sans-serif;
}

@keyframes app-enter-anim {
  from {
    opacity: 0.8;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  to {
    opacity: 1;
    transfrom: scale(1);
  }
}
@-webkit-keyframes app-enter-anim {
  from {
    opacity: 0.8;
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }
  to {
    opacity: 1;
    transfrom: scale(1);
  }
}
